import styled from 'styled-components'
import Header from '../components/Header'

const TitleComponent = ({ className, node }) => <h1 className={className}>{node}</h1>

const Title = styled(TitleComponent)`
  font-size: 14px;
  font-weight: bold;
`

const LayoutComponent = ({ className, children }) => (
  <div className={className}>
    <Header />
    <Title node={children} />
  </div >
)

const Layout = styled(LayoutComponent)`
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
`

export default Layout


